.App {
  text-align: center;
}

.outside-left {
  position: relative;	
	height: 221px;
	background: #FF0000;
	clip-path: polygon(100% 100%, 0% 100%, 0 0, 100% 6%);  
}

.inside-left {
  position: absolute;
  top: 4px;
  left: 4px;
  right: 4px;
  bottom: 4px;
  background: #000;  
  clip-path: polygon(100% 100%, 0% 100%, 0 0, 100% 6%);  
}

.outside-right {
  position: relative;	
	height: 221px;
	background: #FF0000;    
	clip-path: polygon(0 6%, 100% 0, 100% 100%, 0% 100%);   
}

.inside-right {
  position: absolute;
  top: 4px;
  left: 4px;
  right: 4px;
  bottom: 4px;
  background: #000;
  clip-path: polygon(0 6%, 100% 0, 100% 100%, 0% 100%);  
  
}

.burger-box {
  border: 4px solid #FF0000;
  margin-top: 1em;
  box-shadow: inset 0 0 0 #00f, 0 0 60px #FF0000;
}

.BurgerText {
  font-weight: 650;
  font-size: 1.3em;
  padding: 0.5em;
}

.BurgerTextOrder {
  font-weight: 650;
  font-size: 1.3em;
  padding: 0.5em;  
}

.AmtBurger {
  font-size: 1.4em;
  font-weight: 600;
  padding: 0.2em;
}

.BUSDLabel {
  font-size: 1.1em;
  font-weight: 700;
  padding: 0.2em;
}

.BoxBelow {
  border: 4px solid #FF0000;
}

.TopBox {
  border: 5px solid #7dd800;
  box-shadow: inset 0 0 0 #00f, 0 0 40px #7dd800;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  width: 80%;  
  min-height: 30px;
  border-bottom: 0;
  color: "#fff"
}

.BottomBox{
  border: 5px solid #FF0000;
  width: 85%;
  border-radius: 10px;
}

.BoxWallet {
  background-color: #06ff00;
  width: 8em;
  border-radius: 10px;
  color: #000;
  padding: 0.5em;
  margin: auto;
}

.burger-box-curve {
  border: 4px solid #FF0000;
  margin-top: 1em;
  box-shadow: inset 0 0 0 #00f, 0 0 60px #FF0000;
  border-radius: 10px;
  padding: 1em;
}

.verticalLine {
  background-color: #FF0000;
  height: 3em;
  margin: 0 auto;  
  width: 1px;
}

.bill-out {
  border: 1px solid #FF0000;
  border-radius: 10px;
  width: 60%;  
}

/* backgroundColor:"#06ff00", color:"#000", fontWeight:"600", width:"20em" */

.bill-out-btn{
  background-color: #06ff00 !important;
  color: #000 !important;
  font-weight: 600 !important;
  width: 20em !important;
}

.bill-out-btn-disabled {
  background-color: #04b100 !important;
  color: #000 !important;
  font-weight: 600 !important;
  width: 20em !important;
}

.ImageBanner {
  width: 60%;
}

.ApproveDepositInput {
  width: 17.5em;
}

.btnOverflow {
  width: 20em;
}

.ContainerBurgerCurve {
  padding-left: 4em;
  padding-right: 4em;
  padding-bottom: 2em;
}

@media only screen and (max-width: 600px) {
  .ImageBanner{
    width: 100%;
  }
  .burger-box-curve {
    padding: 0;
    width: 100%;    
  }
  .referalText {
    font-size: 11px;
  }
  .referalTextLink {
    font-size: 8px;
  }
  .barImg {
    width: 29%;
    height: auto;
  }
  .ApproveDepositInput {
    width: auto;
  }
  .btnOverflow {
    width: auto;
  }
  .bill-out-btn {
    width: auto !important;
  }
  .bill-out-btn-disabled{
    width: auto !important;
  }
  .ContainerBurgerCurve {
    padding-left: 1.6em;
    padding-right: 1.6em;
    padding-bottom: 2em;
  }
  
}

